.page-header__toggle {
  position: relative;
  width: 24px;
  height: 18px;
  padding: 0;
  font-size: 0;
  background: none;
  background-color: #000000;
  background-clip: content-box;
  border: none;
  border-top: 8px solid transparent;
  border-bottom: 8px solid transparent;
  transition: 0.4s;
}
.page-header__toggle::before,
.page-header__toggle::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  left: 0;
  background-color: #000000;
  transition: 0.4s;
}
.page-header__toggle::before {
  top: -8px;
}
.page-header__toggle::after {
  top: 8px;
}
.page-header__toggle--nojs {
  display: none;
}
.page-header__toggle--open {
  background-color: transparent;
}
.page-header__toggle--open::before,
.page-header__toggle--open::after {
  top: 50%;
  left: 50%;
}
.page-header__toggle--open::before {
  transform: translate(-50%, -50%) rotate(45deg);
}
.page-header__toggle--open::after {
  transform: translate(-50%, -50%) rotate(-45deg);
}
@media (max-width: 767px) {
  .page-header__nav--close {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip: rect(0 0 0 0);
    overflow: hidden;
  }
}
