  width: 22px;
  height: 12px;
}
.social__link--insta svg {
  width: 16px;
  height: 16px;
}
.social__link--fb svg {
  width: 10px;
  height: 19px;
}
@media (min-width: 768px) {
  .social {
    margin-right: -24px;
  }
  .social__item {
    margin-right: 24px;
  }
  .social__link {
    width: 30px;
  }
  .social__link--vk svg {
    width: 29px;
    height: 16px;
  }
  .social__link--insta svg {
    width: 23px;
    height: 23px;
  }
  .social__link--fb {
    text-align: left;
  }
  .social__link--fb svg {
    width: 15px;
    height: 28px;
  }
}
.copyright {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.copyright__author {
  font-family: Arial, sans-serif;
  font-size: 16px;
  line-height: 16px;
  color: #444444;
}
.copyright__author:active {
  opacity: 0.3;
}
.copyright__logo {
  display: block;
  width: 27px;
  height: 34px;
  fill: #666666;
}
.copyright__logo:hover,
.copyright__logo:focus {
  fill: #68b738;
}
.copyright__logo:active {
  opacity: 0.3;
}
