.social {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-top: 0;
  margin-right: -32px;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
}

.social__item {
  margin-right: 32px;
  margin-bottom: 10px;

  &:nth-child(3n) {
    margin-right: 0;
  }
}

.social__link {
  display: block;
  width: 22px;
  font-size: 0;
  line-height: 0;
  text-align: center;
  fill: #666666;

  &:hover,
  &:focus {
    fill: #68b738;
  }

  .clicked;

  &--vk svg {
    width: 22px;
    height: 12px;
  }

  &--insta svg {
    width: 16px;
    height: 16px;
  }

  &--fb svg {
    width: 10px;
    height: 19px;
  }
}

@media (min-width: @tablet-width) {
  .social {
    margin-right: -24px;
  }

  .social__item {
    margin-right: 24px;
  }

  .social__link {
    width: 30px;

    &--vk svg {
      width: 29px;
      height: 16px;
    }

    &--insta svg {
      width: 23px;
      height: 23px;
    }

    &--fb {
      text-align: left;
    }

    &--fb svg {
      width: 15px;
      height: 28px;
    }
  }
}
